"use client";
import Container from "@/components/Container";
import { useSearchParams } from "next/navigation";
import React from "react";

const TureIdPage = ({params}: {params: {id: string}}) => {
  const id = params.id

  return (
    <main>
      <Container>
        <section className="w-[55%]"></section>
        <section className="w-[42.5%]">{id}</section>
      </Container>
    </main>
  );
};

export default TureIdPage;
